import { Component } from '@angular/core';
import { NavController, ActionSheetController, AlertController } from 'ionic-angular';

/* pages */
import { WelcomePage } from "../welcome/welcome";

/* service */
import { LocalStorage } from "../../services/localStorage";

/* repository */
import { CallogRepository } from "../../repository/callogRepository";

/* helper */
import { LogHelper } from "../../helper/logHelper";

/* NgStore */
import { NgRedux } from '@angular-redux/store';
import { PtcListAction } from '../../pages/ptc-list/ptc-list-action'

/**
 * 
 */
@Component({
    selector: 'vender-accept',
    templateUrl: 'vender-accept.html'
})
export class VenderAcceptPage {

    public user: User;

    //
    public items: Array<CallogResultApiViewModel> = [];

    //
    public totalCount: number = 0;

    //
    public pageIndex = 0;

    //
    public InfiniteEnabled: Boolean = true;

    //
    public features = 1;

    //
    public orderText = '';
    public orderBy = '';

    //
    public orderTypeText = '';
    public orderType: number = 0;

    constructor(private nav: NavController,
        private log: LogHelper,
        private local: LocalStorage,
        private actionSheetCtrl: ActionSheetController,
        private callogRepo: CallogRepository,
        private ngRedux: NgRedux<Array<CallogResultApiViewModel>>,
        private action: PtcListAction,
        private alertCtrl: AlertController) { }


    /**
      * ,method
      */
    ionViewWillEnter($event) {

        this.pageIndex = 0;

        this.items = [];

        this.log.info(`--()=>[vendorAccept.ts]`);

        this.getList($event);
    };

    /**
     * 
     */
    doRefresh($event) {

        this.pageIndex = 0
        this.items = [];

        this.InfiniteEnabled = true;

        this.getList($event);


    }


    /**
     * scroll 
     * @param $event
     */
    continuousLoading($event) {

        this.pageIndex++;

        this.getList($event);

    }

    /**
     * 
     */
    getList($event) {

        this.log.info(`--`);

        this.callogRepo.GetAwaitAdoptCallog(
            this.pageIndex,
            this.orderBy,
            this.orderType, 
            $event==null,
            success.bind(this),
            failure.bind(this),
        );

        function success(data: JsonResult<Array<CallogResultApiViewModel>>): void {

            this.log.info(data);

            data.element.forEach(x => this.items.push(x));

            this.action.getListDispatch(this.items);

            this.totalCount = data.totalCount;

            //
            if ($event) $event.complete();

            //,InfiniteScroll
            if ((<JsonResult<Array<CallogResultApiViewModel>>>(data)).totalCount <= this.items.length)
                this.InfiniteEnabled = false;
        };

        function failure(error: HttpResponse): void {

            //
            if (error.status == 401) {
                this.local.set('user', null);
                let alert = this.alertCtrl.create({
                    title: '',
                    subTitle: '',
                    buttons: ['OK']
                });
                alert.present();

                this.nav.setRoot(WelcomePage);
            }

        };
    }



    /**
    * 
    */
    chooseSortBy() {

        let actionSheet = this.actionSheetCtrl.create({
            buttons: [
                {
                    text: '',
                    handler: () => {
                        this.log.info(`--[]`);
                        this.orderText = '';
                        this.orderBy = 'Sn';
                        this.doRefresh(null);

                    }
                },
                {
                    text: '',
                    handler: () => {
                        this.log.info(`--[]`);
                        this.orderText = '';
                        this.orderBy = 'Store_Name';
                        this.doRefresh(null);
                    }
                },
                {
                    text: '',
                    handler: () => {
                        this.log.info(`--[]`);
                        this.orderText = '';
                        this.orderBy = 'Vnd_Total_Cost';
                        this.doRefresh(null);
                    }
                },
                {
                    text: '',
                    handler: () => {
                        this.log.info(`--[]`);
                        this.orderText = '';
                        this.orderBy = 'Asset_Name';
                        this.doRefresh(null);
                    }
                },
                {
                    text: '',
                    handler: () => {
                        this.log.info(`--[]`);
                        this.orderText = '';
                        this.orderBy = 'Rcv_Datetime';
                        this.doRefresh(null);
                    }
                },
                {
                    text: '',
                    handler: () => {
                        this.log.info(`--[]`);
                        this.orderText = '';
                        this.orderBy = 'Fc_Date';
                        this.doRefresh(null);
                    }
                }
            ]
        });
        actionSheet.present();
    }

    /**
     * 
     */
    chooseSortTypeBy() {

        let actionSheet = this.actionSheetCtrl.create({
            buttons: [
                {
                    text: '',
                    handler: () => {
                        this.log.info(`--[${this.orderText},]`);
                        this.orderTypeText = '';
                        this.orderType = 0;
                        this.doRefresh(null);

                    }
                },
                {
                    text: '',
                    handler: () => {
                        this.log.info(`--[${this.orderText},]`);
                        this.orderTypeText = '';
                        this.orderType = 1;
                        this.doRefresh(null);
                    }
                },

            ]
        });
        actionSheet.present();


    }

}



