import { Injectable } from '@angular/core';
import { Platform, App, AlertController } from 'ionic-angular';

/* service */
import { JPushService } from 'ionic2-jpush';
import { LocalStorage } from './localStorage';

/* repository */
import { UserRepository } from '../repository/userRepository';
import { Response } from '@angular/http'

/* service */
import { FileService } from './fileService';
import { VenderConfirmPage } from '../pages/vender-confirm/vender-confirm';
import { VenderAssignPage } from '../pages/vender-assign/vender-assign';
import { VenderAcceptPage } from '../pages/vender-accept/vender-accept';

@Injectable()
export class PushService {


    constructor(private alert: AlertController,
        private app: App,
        private push: JPushService,
        private file: FileService,
        private local: LocalStorage,
        private userRepo: UserRepository,
        private platform: Platform) {

    }


    /**
     * 
     */
    start() {

        this.file.WriteLog('-------------------');

        console.log('----------  -----------');

        this.platform.ready().then(() => {

            //
            this.push.openNotification()
                .subscribe(res => {

                    this.file.WriteLog('----------(openNotification)---------');

                    this.direct(res.extras.FeatureName);

                    console.log(res)
                });

            this.push.receiveNotification()
                .subscribe(res => {

                    this.file.WriteLog('----------(Notification)---------');

                    this.askDirect('', res.alert, res.extras.FeatureName);

                    console.log(res)
                });

            this.push.receiveMessage()
                .subscribe(res => {

                    this.file.WriteLog('----------(Message)---------');

                    this.askDirect('', res.alert, res.extras.FeatureName);

                    console.log(res)
                });

        })

    }

    /**
     * 
     */
    init() {

        this.file.WriteLog('-------------------');

        console.log('----------  -----------');

        this.push
            .init()
            .then(res => {

                this.file.WriteLog(`----------  : ${res} ----------`);

                console.log(`----------  : ${res} ----------`);

            })
            .catch(err => {

                this.file.WriteLog(`----------  : ${err} ----------`);

                console.log(`----------  : ${err} ----------`);

            })
    }



    /**
     * 
     */
    askDirect(title: string, message: string, featureName: string) {

        if (featureName=="")
        {
            let alert = this.alert.create({
                title: title,
                message: message,
                buttons: [
                    {
                        text: '',
                        role: 'cancel',
                        cssClass: 'alertbuttonYes',
                        handler: () => {}
                    }
                ]
            });
            alert.present();
        }
        else
        {
            let alert = this.alert.create({
                title: title,
                message: message,
                buttons: [
                    {
                        text: '',
                        role: 'cancel',
                        cssClass: 'alertbuttonNo',
                        handler: () => {
    
                        }
                    },
                    {
                        text: '',
                        cssClass: 'alertbuttonSubmit',
                        handler: () => {
                            this.direct(featureName);
                        }
                    }
                ]
            });
            alert.present();
        }
    }

    /**
     * 
     */
    direct(FeatureName: string) {

        switch (FeatureName) {

            case 'VenderAccept':
                this.app.getActiveNav().setRoot(VenderAcceptPage);
                break;
            case 'VenderAssign':
                this.app.getActiveNav().setRoot(VenderAssignPage);
                break;
            case 'VenderConfirm':
                this.app.getActiveNav().setRoot(VenderConfirmPage);
                break;
            default:
                break;

        }


    }

    /**
    * tag , RegID
    */
    settingRegID() {

        var user: User = this.local.get<User>('user');

        this.file.WriteLog(`----------  RegisterId  ----------`);

        console.log('----------  RegisterId  -----------');

        this.push
            .getRegistrationID()
            .then(res => {

                this.file.WriteLog(`---------- RegID ${res} ----------`);

                console.log(` ---------- RegID ${res} ----------`);

                this.file.WriteLog(`---------- RegisterId  ----------`);

                console.log(` ---------- RegisterId  ----------`);


                user.RegistrationID = res as string;

                this.local.set('user', user);

                this.file.WriteLog(`---------- RegisterId  ----------`);

                console.log(` ---------- RegisterId  ----------`);

                this.userRepo.UpdateRegID(
                    user.RegistrationID,
                    (data: JsonResult<Boolean>) => console.log(data.message),
                    (err: Response) => console.log(err));

            })
            .catch(err => {

                this.file.WriteLog(`----------   RegID  : ${err} ----------`);

                console.log(` ----------  RegID  : ${err} ----------`);

            })
    }

}



