import { Injectable } from '@angular/core';
import { File, Transfer } from 'ionic-native';
import { LoadingController } from 'ionic-angular';
import { LocalStorage } from './localStorage';


@Injectable()
export class FileService {

    constructor(
        private Loading: LoadingController,
        private local: LocalStorage) { }


    /**
     * log
     * @param msg
     */
    WriteLog(msg: string) {

        console.log('----------LOG----------');

        console.log(`LOG:${msg}`);

        //
        var today = new Date();

        var todayStr: string = today.toISOString().substring(0, 10);

        console.log('--------------------');

        File.resolveDirectoryUrl(this.local.get<string>('logPath') || '').then(dir => {

            console.log('--------------------');

            dir.getFile(`${todayStr}Log.txt`,
                {
                    create: true
                },
                function (success) {

                    var logOb = success;

                    var log = `:: ${new Date().toISOString()} ::  [${msg}]\n`;

                    console.log('--------------------');

                    logOb.createWriter(function (fWriter) {

                        fWriter.seek(fWriter.length);

                        var blob = new Blob([log], { type: 'text/plain' });

                        fWriter.write(blob);

                    }, function (error) {

                        console.log(`----------,=>${error}----------`);

                    });

                }, function (error) {

                    console.log(`----------,=>${error}----------`);

                })


        }).catch(error => {

            console.log(`----------,=>${error.message}----------`);

        });


    }

    /**
     * 
     * @param filePath
     * @param fileNames
     */
    Remove(filePath: string, fileNames: Array<string>) {

        let loadingPopup = this.Loading.create({ content: '...' });

        console.log(`----------  ----------`)

        loadingPopup.present();

        File.resolveDirectoryUrl(filePath).then(dir => {

            fileNames.forEach(filename => {

                console.log(`--,:${filename}`);

                dir.getFile(filename,
                    {
                        create: true
                    },
                    function (success) {

                        success.remove(() => {

                            console.log(`--,:${filename}`);

                        }, (error) => {

                            console.log(`--,:${error}`);

                            this.WriteLog(`--,:${error}`);
                        });


                    }, function (error) {

                        console.log(`----------,=>${error}----------`);

                        this.WriteLog(`--,=>${error}----------`);
                    })
            });

            loadingPopup.dismiss();

        }).catch(error => {

            console.log(`----------,=>${error}----------`);

            this.WriteLog(`--,=>${error}----------`);

            loadingPopup.dismiss();

        });


    }

    /**
     * 
     * @param url 
     */
    // Download(url: string , filename:string):Promise<Entry> {

    // var fullPath =   (this.local.get<string>('logPath') || '') + filename;

    // console.log(`,url:${url}`);
    // console.log(`,:${fullPath}`);

    // return new Transfer().download(url, fullPath);
        

    // }


}


